@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
body {
	margin: 0;
	font-family: "Montserrat", sans-serif;
	font-weight: bolder;
}

.header {
	overflow: hidden;
	background-color: rgb(241, 241, 241);
	padding: 20px 10px;
	border-bottom: 2px solid black;
}

.basket-icon {
	color: black;
	padding: 0px 20px;
	cursor: pointer;
}

.basket-icon:hover {
	color: rgb(93, 1, 179);
}

.container {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
}

.content {
	flex: 1 1;
}

.footer {
	bottom: 0;
	position: relative;
	width: 100%;
	background-color: rgb(241, 241, 241);
	padding: 20px 0px;
	margin-bottom: 0px;
	margin-top: 20px;
	display: flex;
	justify-content: center;
	border-top: 2px solid black;
}

.social {
	cursor: pointer;
	color: black;
}

.social:hover {
	color: rgb(97, 12, 52);
}

.title {
	text-align: center;
	font-size: 300%;
	display: flex;
	align-items: center;
	justify-content: space-around;
}

@media only screen and (max-width: 700px) {
	[class="title"] {
		font-size: 200%;
	}
}

.basket-badge {
	display: flex;
}

.grid {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.img {
	max-height: 250px;
}

.product {
	margin: 5px;
	max-width: 250px;
}

.product:hover {
	background-color: rgb(241, 241, 241);
}

.details {
	display: grid;
	grid-template-columns: 70% 30%;
	padding: 0px 5px;
}

/* prevents long product names (or high price tags!) from
overflowing onto the next line */

.name,
.price {
	display: block;
	overflow: hidden;
	white-space: nowrap;
	text-align: left;
	text-overflow: ellipsis;
}

.price {
	text-align: right;
}

button {
	background-color: white;
	font-weight: bolder;
	font-family: "Montserrat", sans-serif;
	font-size: 100%;
	display: block;
	width: 100%;
	height: 40px;
	border: 2px solid black;
	padding: 0px;
}

button:hover {
	background-color: black;
	color: white;
	transition: 0.4s;
}

.navBar {
	overflow: hidden;
	border-bottom: 2px solid black;
	display: flex;

	justify-content: center;
}

.nav-btn {
	width: 80px;
	margin: 5px 5px;
}

.modal {
	display: flex;
	position: fixed;
	left: 0;
	top: 0;
	align-items: center;
	justify-content: center;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.3);
}

.cart,
.filter-menu {
	position: relative;
	width: 650px;
	max-height: 100%;
	overflow-y: auto;
	padding: 0px 20px 0px 20px;
	background-color: white;
}

.filter-menu {
	width: 400px;
}

.close-btn {
	position: absolute;
	top: 20px;
	right: 20px;
	cursor: pointer;
}

.cart-title {
	border-bottom: 1px solid black;
}

.cart-grid {
	display: grid;
	grid-template-columns: 25% 25% 25% 25%;
	padding: 10px;
}

@media only screen and (max-width: 700px) {
	[class="cart-grid"] {
		grid-template-columns: auto auto;
	}
}

.img-cart {
	max-height: 120px;
	border-radius: 10%;
}

.cart-item {
	display: flex;
	justify-content: space-between;
	padding: 20px;
}

.icon {
	cursor: pointer;
	size: 28px;
	display: inline;
	color: black;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	vertical-align: text-bottom;
}

.icon:hover {
	color: rgb(93, 1, 179);
}

.add {
	padding-right: 10px;
}

.remove {
	padding-left: 10px;
}

.total {
	font-size: 200%;
	text-align: right;
}

.black-btn {
	background-color: black;
	color: white;
	margin-bottom: 15px;
	transition: 0.4s;
}

.black-btn:hover {
	background-color: white;
	color: black;
}

[type="checkbox"] {
	opacity: 0;
}

.filter-checkbox {
	display: block;
	position: relative;
	cursor: pointer;
	padding: 0px 20px;
	margin: 20px 10px;
}

.checkmark {
	content: "";
	width: 1.3em;
	height: 1.3em;
	position: absolute;
	left: 0;
	top: 0;
	outline: 2px solid black;
	display: block;
}

.checkmark:hover {
	background-color: black;
}

[type="checkbox"]:checked ~ .checkmark {
	background-color: black;
}

.checkmark::after {
	opacity: 0;
	content: "";
	position: absolute;
	left: 0.4em;
	top: 0.2em;
	width: 0.3em;
	height: 0.6em;
	border-right: 3px solid white;
	border-bottom: 3px solid white;
	-webkit-transform: rotate(45deg) scale(3);
	        transform: rotate(45deg) scale(3);
	transition: all 0.3s ease;
}

[type="checkbox"]:checked ~ .checkmark::after {
	opacity: 1;
	-webkit-transform: rotate(45deg) scale(1);
	        transform: rotate(45deg) scale(1);
}

